@import url('../../variable.scss');


//Mobile View
.all-items-section {
    margin: 5%;
}

.checkout-title-grid {
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    text-align: center;
    font: normal normal 600 36px/45px Source Serif Pro;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
}

.checkout-grid-divider {
    /* UI Properties */
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #E26A2C 0% 0% no-repeat padding-box;
    opacity: 1;
    width: 20%;
    height: 8px;
    margin: 0 auto;
}

.heading-main {
    margin-bottom: 20px;
}

.left-side-txt h1 {
    /* UI Properties */
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-24)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-source-serif-pro);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    text-align: left;
    font: normal normal 600 24px/30px Source Serif Pro;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
}

.place-order-btn-desk {
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #E26A2C 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px rgb(57 55 55 / 16%);
    opacity: 1;
    height: 40px;
    width: 100%;
    border: none;
    margin: 5% auto;
    color: #fff;
    display: block;
}

.place-term-condition {
    /* UI Properties */
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    text-align: center;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
}

a {
    text-decoration: none;
}

//Deskto View
@media all and (min-width:769px) {
    .all-items-container {
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 1em;
        margin: 5%;
    }

    a {
        text-decoration: none;
    }

    .checkout-title-grid {
        /* UI Properties */
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-48)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        text-align: center;
        font: normal normal 600 48px/45px Source Serif Pro;
        letter-spacing: 0px;
        color: #172026;
        opacity: 1;
    }

    .checkout-grid-divider {
        /* UI Properties */
        background: var(--primary-brand) 0% 0% no-repeat padding-box;
        background: #E26A2C 0% 0% no-repeat padding-box;
        opacity: 1;
        width: 10%;
        height: 8px;
        margin: 0 auto;
        margin-top: 7px;
    }

    .left-side-txt h1 {
        /* UI Properties */
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-source-serif-pro);
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        text-align: left;
        font: normal normal 600 24px/30px Source Serif Pro;
        letter-spacing: 0px;
        color: #172026;
        opacity: 1;
    }

    .left-side-txt {
        grid-column: 1/-1;
        grid-row: 1/2;
    }

    .heading-main {
        margin: 2% 0;
    }

    .place-order-btn {
        display: none;
    }

    .place-order-btn-desk {
        /* UI Properties */
        background: var(--primary-brand) 0% 0% no-repeat padding-box;
        background: #E26A2C 0% 0% no-repeat padding-box;
        box-shadow: 0px 3px 6px #39373729;
        opacity: 1;
        height: 40px;
        width: 50%;
        border: none;
        margin: 5% auto;
        color: #fff;
        display: block;
    }

    .all-items-section {
        margin: 0% !important;
    }




}